import {
  Radio,
  RadioGroup,
  Alert,
  AlertIcon,
  Stack,
  Text,
  Input,
  Box,
  useDisclosure,
  Button,
  Collapse,
  Code,
} from '@chakra-ui/react'
import React, { useRef, useState } from 'react'
export const Settings = ({
  inputEl,
  setNewLine,
  setQuestionType,
  showSetting,
  createPreview,
}) => {
  const { isOpen, onToggle } = useDisclosure()
  const inputNewLine = useRef()
  const inputAnswerEnd = useRef()
  const inputAnswerStart = useRef()
  const inputChoiceStart = useRef()
  const inputChoiceEnd = useRef()
  const inputQuestionEnd = useRef()
  const inputQuestionStart = useRef()
  const [infoTxt, setInfoTxt] = useState()
  const setting = showSetting()
  let nl = `'${setting.newLine}'`
  const checkForm = (str) => {
    let con = ''
    if (str) {
      con = new RegExp(str, 'g')
    } else {
      return
    }
    if (inputEl.current.value && inputEl.current.value.match(con)) {
      let count = inputEl.current.value.match(con).length
      setInfoTxt(`${str}${count}`)
    } else {
      setInfoTxt(`${str}`)
    }
  }
  return (
    <>
      <Alert status="info">
        <AlertIcon />
        {infoTxt}
      </Alert>
      <Text></Text>
      <RadioGroup>
        <Stack direction="row" bgColor={'blue.100'} defaultValue="forms">
          <Radio
            value="1"
            onChange={() => {
              setQuestionType('forms')
              createPreview(setting, inputEl)
              setInfoTxt('' + setting.questionType)
            }}
          >
            MS-Forms
          </Radio>
          <Radio
            value="2"
            onChange={() => {
              setQuestionType('filling')
              createPreview(setting, inputEl)
              setInfoTxt('' + setting.questionType)
            }}
          >
            
          </Radio>
          <Radio
            value="3"
            onChange={() => {
              setQuestionType('forms')
              createPreview(setting, inputEl)
              setInfoTxt('' + setting.questionType)
            }}
          >
            
          </Radio>
        </Stack>
      </RadioGroup>
      <Text></Text>
      <RadioGroup>
        <Stack direction="row" bgColor={'blue.100'}>
          <Radio
            value="1"
            onChange={() => {
              setNewLine(1)
              createPreview(setting, inputEl)
              setInfoTxt('/\n/g')
            }}
          >
            \n
          </Radio>
          <Radio
            value="2"
            onChange={() => {
              setNewLine(2)
              createPreview(setting, inputEl)
              setInfoTxt('/\ns+/g')
            }}
          >
            \n\s+
          </Radio>
          <Radio
            value="3"
            onChange={() => {
              setNewLine(3)
              createPreview(setting, inputEl)
              setInfoTxt('/s+/g')
            }}
          >
            
          </Radio>
          <Input
            placeholder=") \n+\s*"
            width={'140px'}
            size="sm"
            variant="filled"
            onChange={() => checkForm(inputNewLine.current.value)}
            ref={inputNewLine}
          />
        </Stack>
      </RadioGroup>
      <Button onClick={onToggle}></Button>
      <Collapse in={isOpen} animateOpacity>
        <Box
          p="40px"
          color="white"
          mt="4"
          bg="blue.700"
          rounded="md"
          shadow="md"
        >
          <Text size={'xs'} ml="-4">
            
          </Text>
          <Text size={'xs'}>
            detailInfo:<Code>{nl}</Code>,
          </Text>
          <Text size={'xs'}>questionImg:[],</Text>
          <Text size={'xs'} maxW="180px">
            questionSentence:
            <Input
              placeholder=") [1-9]\."
              width={'80px'}
              size="xs"
              variant="outline"
              onChange={() => checkForm(inputQuestionStart.current.value)}
              ref={inputQuestionStart}
            />
            <Code>Aorta...</Code>
            <Input
              placeholder=") \([1-9]+\)"
              width={'120px'}
              size="xs"
              variant="outline"
              onChange={() => checkForm(inputQuestionEnd.current.value)}
              ref={inputQuestionEnd}
            />
            ,
          </Text>
          <Text size={'xs'} maxW="180px">
            choices:
            <Input
              placeholder=") [a-z]\."
              width={'80px'}
              size="xs"
              variant="outline"
              onChange={() => checkForm(inputChoiceStart.current.value)}
              ref={inputChoiceStart}
            />
            <Code></Code>
            ,,
          </Text>
          <Text size={'xs'}>answerImg:[],</Text>
          <Text size={'xs'}>
            answer:{' '}
            <Input
              placeholder=") A."
              width={'80px'}
              size="xs"
              variant="outline"
              onChange={() => checkForm(inputAnswerStart.current.value)}
              ref={inputAnswerStart}
            />
            <Code>...</Code>
            <Input
              placeholder=") [1-9]\."
              width={'80px'}
              size="xs"
              variant="outline"
              onChange={() => checkForm(inputAnswerEnd.current.value)}
              ref={inputAnswerEnd}
            />
            ,
          </Text>
          <Text size={'xs'}>commentary:'',</Text>
          <Text ml={-2} size={'xs'}>
            ,
          </Text>
        </Box>
      </Collapse>
    </>
  )
}
